@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --primary-bg-color: #f1f5f8;
  --btn-color: rgb(238, 238, 238);

  --primary-text-color: #102a42;
  --secondary-text-color: #324d67;

  --btn-primary-color: rgb(2, 151, 151);
  --btn-hover-color: rgb(2, 124, 124);
}

.App {
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: #f1f5f8;
  background-color: var(--primary-bg-color);
}

.container {
  padding: 2rem 4rem;
  border-radius: 5px;
  background-color: white;
  width: 600px;
  box-shadow: 0 10px 12px rgba(0, 0, 0, 0.05);
  position: relative;
}

.container > h1 {
  margin: 1rem 0 3rem 0;
  color: #102a42;
  color: var(--primary-text-color);
  text-align: center;
  letter-spacing: 1px;
}

/* Setup Quiz styling section  */
/* <~------------- ------------ >  */
label {
  color: #324d67;
  color: var(--secondary-text-color);
  font-size: 16.5px;
}

input[type="number"] {
  margin: 0.5rem 0 2rem 0;
  padding: 0.5rem 1rem;
  width: 100%;
  border: none;
  background-color: rgb(238, 238, 238);
  background-color: var(--btn-color);
  border-radius: 5px;
  font-size: 16px;
}

select {
  margin: 0.5rem 0 2rem 0;
  width: 100%;
  border: none;
  border-radius: 5px;
  background-color: rgb(238, 238, 238);
  background-color: var(--btn-color);
  padding: 0.5rem;
  font-size: 16px;
}

.start-btn {
  padding: 0.5rem;
  background-color: rgb(2, 151, 151);
  background-color: var(--btn-primary-color);
  color: white;
  border: none;
  border-radius: 3px;
  font-size: 18px;
  margin: 1rem 0;
  width: 100%;
  cursor: pointer;
}

.start-btn:hover {
  background-color: rgb(2, 124, 124);
  background-color: var(--btn-hover-color);
}

/* Result section styling  */
/* ------------------------- */
.cross-btn {
  position: absolute;
  top: 15px;
  left: 15px;
  font-size: 26px;
  color: #ce3c3c;
  cursor: pointer;
}

.result-main {
  padding: 2rem 0;
  text-align: center;
}
.result-main > h1 {
  color: #102a42;
  color: var(--primary-text-color);
  letter-spacing: 2px;
  margin-bottom: 20px;
}
.result-main > h3 {
  color: #324d67;
  color: var(--secondary-text-color);
  letter-spacing: 1px;
}
.result-main span {
  background-color: #102a42;
  background-color: var(--primary-text-color);
  border-radius: 50%;
  padding: 0.4rem 0.85rem;
  color: white;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary-bg-color: #f1f5f8;
  --btn-color: rgb(238, 238, 238);

  --primary-text-color: #102a42;
  --secondary-text-color: #324d67;

  --primary-color: rgb(2, 151, 151);
  --btn-hover-color: rgb(2, 124, 124);
}

/* Quiz page styling section  */
/* <............. ........>  */
.question-title {
  text-align: center;
  margin-top: 10px;
}

.list-container {
  margin: 2rem 0;
  list-style-type: none;
}

.item-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border: 1.5px solid rgb(213, 213, 213);
  width: 100%;
  border-radius: 8px;
  margin: 1rem 0;
  font-size: 18px;
  color: #102a42;
  color: var(--primary-text-color);
  cursor: pointer;
}

.select-btn {
  border: none;
  transition: all 100ms ease-in;
  background: none;
  cursor: pointer;
}

.header-container {
  color: rgb(2, 151, 151);
  color: var(--primary-color);
  font-size: 16px;
  display: flex;
  justify-content: space-between;
  margin: 0 0 2rem 0;
}

.header-container > div:nth-child(2) {
  display: flex;
}

.header-container span {
  font-size: 16px;
  background-color: rgb(2, 151, 151);
  background-color: var(--primary-color);
  border-radius: 3px;
  color: white;
  padding: 1px 6px;
  margin-left: 5px;
}

.back-btn {
  font-size: 16px;
  background: none;
  border-radius: 3px;
  color: rgb(2, 151, 151);
  color: var(--primary-color);
  margin-left: 5px;
  border: none;
  cursor: pointer;
}

.btn-container {
  margin: 1rem 0;
  display: flex;
  justify-content: space-between;
}

.item {
  font-size: 15.5px;
}

.btn {
  width: 40px;
  height: 35px;
  color: white;
  background-color: rgb(2, 151, 151);
  background-color: var(--primary-color);
  border-radius: 4px;
  border: none;
  font-size: 20px;
  font-weight: 400;
  cursor: pointer;
  margin: 0 0.1rem;
}

